.pagination {
  display: flex;
  padding: 0;
  margin: 0 6px;
  list-style: none;
}
.pagination__item a {
  color: #ffffff;
  padding: 10px 15px;
  background-color: #000000;
}
.pagination__item a:hover, .pagination__item a:focus, .pagination__item a:active {
  background-color: #663d15;
}
.pagination__item {
  margin: 0 5px;
}
.pagination__item--current a {
  color: #000000;
  background-color: #ffffff;
}
.pagination__item--current a:hover, .pagination__item--current a:focus, .pagination__item--current a:active {
  color: #000000;
  background-color: #ffffff;
}